  font-weight: 700;
  transition: .4s;
}

.rederect-link:hover {
  color: #1a36eb;
  text-decoration: none;
}

.p-color-reset {
  color: inherit;
}

img {
  max-width: 100%;
}

/*----- Component / Breadcrumb ----*/
.doc-breadcrumb .title {
  font-size: 45px;
  line-height: 50.6px;
  font-weight: 700;
  color: #2C3038;
}

.doc-breadcrumb p {
  font-size: 20px;
  line-height: 33.8px;
  color: #555A64;
}

/*----- Component / Sidebar ----*/
.h1, .h1, .h3, .h4, .h5, .h6 {
  color: #2C3038;
  font-weight: 700;
}

p {
  color: #555A64;
}

/*----- Component / alert ----*/
.alert p {
  color: inherit;
}

.alert-info p {
  margin-bottom: 0;
}

/*----- Layout / Sidebar ----*/
.doc-sidebar--left {
  position: fixed;
  left: 0;
  top: 0;
  display: block !important;
  height: 100% !important;
  background: #f7f9fc !important;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 30px;
  padding-bottom: 30px;
  overflow: unset !important;
  overflow-y: scroll !important;
}

@media (min-width: 320px) and (max-width: 992px) {
  .doc-sidebar--left {
    width: 285px;
    z-index: 999;
    left: -100%;
    transition: all 800ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
    visibility: hidden;
  }
}

.doc-sidebar--left::-webkit-scrollbar {
  width: 10px;
}

.doc-sidebar--left::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.doc-sidebar--left::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}

.doc-sidebar--left::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@media (min-width: 320px) and (max-width: 992px) {
  .doc-sidebar--left.collapse.show {
    left: 0%;
    visibility: visible;
  }
}

@media (min-width: 320px) and (max-width: 992px) {
  .doc-sidebar--left.collapsing {
    transition: height 0s, left 800ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
    height: 100%;
    visibility: visible;
  }
}

.sidebar-brand-block {
  text-align: center;
  padding-bottom: 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #ccc;
}

.sidebar-brand-block a {
  width: 50px;
}

@media (min-width: 992px) {
  .sidebar-brand-block a {
    width: 85px;
  }
}

.sidebar-menu-group .menu-title {
  font-weight: 700;
  line-height: 1.2;
  color: #2c3038;
  margin-bottom: 15px;
  font-size: 18px;
}

.sidebar-menu li a {
  color: #555a64;
  font-size: 15px;
  line-height: 22.5px;
  font-weight: 600;
  padding: 5px 0px;
  transition: all .2s ease-in-out;
}

.sidebar-menu li a:hover {
  text-decoration: none;
}

.sidebar-menu li:not(:hover) {
  opacity: .5;
}

/*----- Layout / Topbar ----*/
.doc-topbar {
  background: #2c3038;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 800;
}

.doc-topbar .buy-button {
  display: none;
}

@media (min-width: 480px) {
  .doc-topbar .buy-button {
    display: block;
  }
}

.doc-topbar .mobile-toggle-btn {
  display: block;
  margin-top: 8px;
}

@media (min-width: 992px) {
  .doc-topbar .mobile-toggle-btn {
    display: none;
  }
}

.doc-topbar .navbar {
  z-index: 800;
}

.mobile-toggle-btn {
  background: none;
  border-width: 0;
  outline: none !important;
}

.mobile-toggle-btn .hamburger-box {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
}

.mobile-toggle-btn .hamburger-box .hamburger-inner {
  position: absolute;
  width: 40px;
  height: 4px;
  top: auto;
  bottom: 0;
  transition-property: transform;
  transition-delay: .13s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition-duration: .13s;
  border-radius: 4px;
  margin-top: -2px;
  background-color: #fff;
}

.mobile-toggle-btn .hamburger-box .hamburger-inner:after, .mobile-toggle-btn .hamburger-box .hamburger-inner:before {
  position: absolute;
  display: block;
  content: "";
  width: 40px;
  height: 4px;
  transition-timing-function: ease;
  transition-duration: .15s;
  transition-property: transform;
  border-radius: 4px;
  background-color: #fff;
}

.mobile-toggle-btn .hamburger-box .hamburger-inner:before {
  transition: top 0.12s cubic-bezier(0.33333, 0.66667, 0.66667, 1) 0.2s, transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  top: -10px;
}

.mobile-toggle-btn .hamburger-box .hamburger-inner:after {
  top: -20px;
  transition: top 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1) 0.2s, opacity 0.1s linear;
  bottom: -10px;
}

.mobile-toggle-btn[aria-expanded="true"] .hamburger-inner {
  transition-delay: .22s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transform: translate3d(0, -10px, 0) rotate(-45deg);
}

.mobile-toggle-btn[aria-expanded="true"] .hamburger-inner:before {
  top: 0;
  transition: top 0.1s cubic-bezier(0.33333, 0, 0.66667, 0.33333) 0.16s, transform 0.13s cubic-bezier(0.215, 0.61, 0.355, 1) 0.25s;
  transform: rotate(-90deg);
}

.mobile-toggle-btn[aria-expanded="true"] .hamburger-inner:after {
  top: 0;
  transition: top 0.2s cubic-bezier(0.33333, 0, 0.66667, 0.33333), opacity 0.1s linear 0.22s;
  opacity: 0;
}

