/*=====================================*/

/*- :::: Section Padding Spacing :::: -*/

/*=====================================*/

.section-padding-bottom {
    padding-bottom: 50px;
    @include tablet {
        padding-bottom: 80px;
    }
    @include desktops {
        padding-bottom: 120px;
    }
}
.section-padding-top {
    padding-top: 50px;
    @include tablet {
        padding-top: 80px;
    }
    @include desktops {
        padding-top: 120px;
    }
}
/*=====================================*/

/*- :::: Space Decrease :::: -*/

/*=====================================*/

@for $i from 0 through 30 {
  .mb-d-#{5 * $i} {
      margin-bottom: -5px *$i;
  }
}

@for $i from 0 through 30 {
  .mt-d-#{5 * $i} {
      margin-top: -5px *$i;
  }
}

/*- Responsive Space Decrease top -*/

@for $i from 0 through 40 {
  .space-dt-sm-#{$i * 5} {
      @include mobile-lg {
          margin-top: #{($i * -5)}px !important;
      }
      @include tablet {
          margin-top: #{($i * -5)}px;
      }
  }
}

@for $i from 0 through 40 {
  .space-dt-md-#{$i * 5} {
      @include tablet {
          margin-top: #{($i * -5)}px !important;
